import React, {useState} from "react";
import { Link } from "react-router-dom";
import SignUp from './SignUp';
import styles from '../css/Menu.module.css';
/*import 'bootstrap/dist/css/bootstrap.min.css';*/

function Menu(){
    const [signUpOn, setSignUpOn] = useState(false);
    return(
      <>
      <SignUp show={signUpOn} 
        onHide={()=>setSignUpOn(false)}
      /> 
      <div className={styles['menu-area']}>
        <div className={styles['h-top']}>
          <div className={styles['jordan-logo']}>
            <Link to="/">
              <img src="https://easy212.github.io/reactNike/images/AIR-JORDAN-LOGO.svg" alt=""/>
              <span>Jordan</span>
            </Link>
          </div>
          <div className={styles['top-menu-area']}>
            <h4 className={styles['top-menu-title']}>     </h4>
            <ul className={styles['top-menu']}>
              <li><Link to="#"></Link></li>
              <li><Link onClick={() => setSignUpOn(true)}></Link></li>
              <li><Link to="#"></Link></li>
            </ul>
          </div>
          <ul className={styles.customer}>
            <li><Link to="#"><img src="images/shopping-bag.svg" alt=""/></Link></li>
            <li><Link to="#"><img src="images/truck.svg" alt=""/></Link></li>
            <li><Link to="#"><img src="images/help-circle.svg" alt=""/></Link></li>
          </ul>
        </div>
      </div>
      </>
    )
}



export default Menu