import React from "react";
import styles from '../css/Detail_img.module.css';


function Detail_img(){
    return(
        <div className={styles.detailLeft}>
            <div className={styles.detailWrap}>
                <div className={styles.detailImg}>
                    <img src="https://easy212.github.io/reactNike/images/SK1001_1.png" alt=""/>
                </div>
                <div className={styles.detailImg}>
                    <img src="https://easy212.github.io/reactNike/images/SK1001_2.png" alt=""/>
                </div>
                <div className={styles.detailImg}>
                    <img src="https://easy212.github.io/reactNike/images/SK1001_3.png" alt=""/>
                </div>
                <div className={styles.detailImg}>
                    <img src="https://easy212.github.io/reactNike/images/SK1001_4.png" alt=""/>
                </div>
            </div>
        </div>
    )
}
export default Detail_img