import React from 'react'
import { Modal } from 'react-bootstrap'
/*import 'bootstrap/dist/css/bootstrap.min.css'*/
import '../css/SignUp.css'

const SignUp = ({show, onHide}) => {
  return (
    <Modal
      show = {show}
      onHide = {onHide}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          <h2></h2>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <form action="" method="post" id="joinForm" name="joinForm">
          <div class="formSign">
            <p><strong class="require"></strong>    .</p>
          </div>
          <div class="baseInfo">
            <h3></h3>
            <fieldset>
              <label for="name"><strong class="require"></strong></label>
              <input type="text" id="name" name="name" placeholder=" "/>
            </fieldset>
            <fieldset>
              <label for="id"><strong class="require"></strong></label>
              <input type="text" id="id" name="id" minlength="4" maxlength="16" size="30" required placeholder="   4~16"/>
              <a href="pop_idCheck.html" class="idCheck">ID </a>
            </fieldset>
            <fieldset>
              <label for="pw"><strong class="require"></strong></label>
              <input type="password" id="pw" name="pw" minlength="10" size="30" required placeholder="   10"/>
            </fieldset>
            <fieldset>
              <label for="confirmPw"><strong class="require"></strong></label>
              <input type="password" id="confirmPw" name="confirmPw"  minlength="10" size="30" required/>
            </fieldset>

            <fieldset class="tel_selct">
              <label></label>
              <select id="hp1" name="hp1">
                <option value="010">010</option>
                <option value="017">017</option>
                <option value="019">019</option>
                <option value="011">011</option>
                <option value="018">018</option>
                <option value="016">016</option>
              </select> - <input type="text" id="hp2" name="hp2"/>
            </fieldset>
            
            <fieldset>
              <label for="email"><strong class="require"></strong></label>
              <input type="email" id="email" name="email" size="30" required placeholder=" ) easy@naver.com"/>
            </fieldset>
          </div>

          <div class="addInfo">
            <h3></h3>
            <fieldset class="sex" >
              <label></label>
              <input type="radio" id="sexInfo1" name="sexInfo" value="" checked/>
              <label></label>
              <input type="radio" id="sexInfo2" name="sexInfo" value=""/>
              <label></label>
            </fieldset>

            <fieldset class="birth">
              <label></label>
              <input type="date" id="birth" name="birth" />
            </fieldset>
            
            <fieldset class="region">
              <label></label>
              <select name="region">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </fieldset>

            <fieldset class="shoesSize">
              <label> </label>
              <select name="shoesSize">
                <option value=""></option>
                <option value="220">220</option>
                <option value="225">225</option>
                <option value="230">230</option>
                <option value="235">235</option>
                <option value="240">240</option>
                <option value="245">245</option>
                <option value="250">250</option>
                <option value="255">255</option>
                <option value="260">260</option>
                <option value="265">265</option>
                <option value="270">270</option>
                <option value="275">275</option>
                <option value="280">280</option>
                <option value="285">285</option>
                <option value="290">290</option>
                <option value="295">295</option>
                <option value="300">300</option>
              </select>
            </fieldset>
            <div class="btnJoinArea">
              <button type="submit" class="btnOk"></button>
            </div>
          </div>
        </form>
      </Modal.Body>
      <Modal.Footer>
      
      </Modal.Footer>
    </Modal>

  )
}

export default SignUp
